@font-face {
  font-family: "Montserrat";
  src: url("../../fonts/Montserrat-VariableFont_wght.ttf");
}
.navMain {
  max-width: 100vw;
  padding: 3vh 15vw 3vh 15vw;
}

ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 4vw;
  list-style: none;
}

.navLink {
  padding: 0.5vw 1vw;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 6px;
  border: 1px solid #DDD;
  color: #8B8B8B;
  text-align: center;
  font-family: Montserrat;
  font-size: 0.8vw;
  font-style: normal;
  font-weight: 500;
}

.navLink.active {
  color: #282828;
  text-align: center;
  font-family: Montserrat;
  font-size: 0.8vw;
  font-style: normal;
  font-weight: 500;
}

@media screen and (max-width: 1024px) {
  .navLink,
  .navLink.active {
    font-size: 1vw;
    padding: 1vw 2vw;
  }
}
@media screen and (max-width: 768px) {
  .navLink,
  .navLink.active {
    font-size: 3vw;
    padding: 1.2vw 2.4vw;
  }
  ul {
    gap: 6vw;
  }
}